import React, { Component } from 'react';
import { Map, CircleMarker, TileLayer, Tooltip, Marker, Popup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import bridge from "@vkontakte/vk-bridge";

class MiniMapComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            lat: 55.753960,
            long: 37.620393,
            zoom: 12,
            marker: null
        };
    }

    componentDidMount() {
        if (this.props.coords.lat !== 0){
            this.setState({lat: this.props.coords.lat, long: this.props.coords.long, zoom: this.props.coords.zoom})
        }
    }

    addMarker = (e, marker) => {
        const newMarker = e.latlng
        this.setState({marker: newMarker})
        if (marker && marker.leafletElement) {
            marker.leafletElement.openPopup()
        }
    }

    render() {
            return (
                <div style={{ height: "200px", width: "100%", scroll: "noscroll"}}>
                    <Map
                        zoomControl={false}
                        onClick={this.addMarker}
                        style={{ height: "200px", width: "100%", position: 'absolute'}}
                        zoom={this.state.zoom}
                        center={[this.state.lat, this.state.long]}>
                        <TileLayer url="http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
                        {
                            this.state.marker !== null &&
                            <CircleMarker
                                draggable={true}
                                center={this.state.marker}
                            >
                                <Tooltip permanent>
                                    <span>Здесь</span>
                                </Tooltip>
                            </CircleMarker>

                        }
                    </Map>
                </div>
            );
        }
}

export default MiniMapComponent;