import React, { useState, useEffect } from 'react';
import {CardGrid, Card, Group, FormLayout, Radio, Header, Button, File} from '@vkontakte/vkui';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon36Add from '@vkontakte/icons/dist/36/add';
import MiniMapComponent from './minimap';
import App from "../App";
import Icon28DeleteOutline from '@vkontakte/icons/dist/28/delete_outline';

const Add = (props) => {
    const [photo1, setPhoto1] = useState(null);
    const [photo2, setPhoto2] = useState(null);
    const [photo3, setPhoto3] = useState(null);
    function handleFileSelect(evt) {
        let file = evt.target.files; // FileList object
        let f = file[0];
        // Only process image files.
        if (!f.type.match('image.*')) {
            console.log("Image only please....");
        }
        let reader = new FileReader();
        // Closure to capture the file information.
        reader.onload = ((theFile) => {
            return function(e) {
                if (photo1 === null){
                    setPhoto1('url('+e.target.result +')');
                } else if (photo2 === null){
                    setPhoto2('url('+e.target.result +')');
                } else if (photo3 === null){
                    setPhoto3('url('+e.target.result +')');
                }
            };
        })(f);
        // Read in the image file as a data URL.
        reader.readAsDataURL(f);
    }
    return (
        <React.Fragment>
            <Group header={<Header mode="secondary">Сделайте отметку на карте</Header>}>
                <MiniMapComponent coords={props.coords} height={'400px'}/>
            </Group>
        <Group separator="hide" header={<Header mode="secondary">Добавьте фотографии</Header>}>
            <CardGrid>
                <Card size="s" mode="shadow">
                    <div style={{ height: 96, backgroundPosition: 'center', backgroundSize: 'cover', backgroundImage: photo1}}>
                        {
                            photo1 === null &&
                            <File onChange={(event)=>handleFileSelect(event)} style={{height: '100%', width: '100%', backgroundColor: "#00ffff00"}} top="" controlSize="xl">
                                <Icon36Add style={{marginTop: '19px', color: "#9ec1ea"}}/>
                            </File>
                        }
                    </div>
                    {
                        photo1 !== null &&
                        <div style={{color: 'red', textAlign: 'center'}} onClick={()=>setPhoto1(null)}>Удалить</div>
                    }
                </Card>
                <Card size="s" mode="shadow">
                    <div style={{ height: 96, backgroundPosition: 'center', backgroundSize: 'cover', backgroundImage: photo2}}>
                        {
                            photo2 === null &&
                            <File onChange={(event)=>handleFileSelect(event)} style={{height: '100%', width: '100%', backgroundColor: "#00ffff00"}} top="" controlSize="xl">
                                <Icon36Add style={{marginTop: '19px', color: "#9ec1ea"}}/>
                            </File>
                        }
                    </div>
                    {
                        photo2 !== null &&
                        <div style={{color: 'red', textAlign: 'center'}} onClick={()=>setPhoto2(null)}>Удалить</div>
                    }
                </Card>
                <Card size="s" mode="shadow">
                <div style={{ height: 96, backgroundPosition: 'center', backgroundSize: 'cover', backgroundImage: photo3}}>
                    {
                        photo3 === null &&
                        <File onChange={(event)=>handleFileSelect(event)} style={{height: '100%', width: '100%', backgroundColor: "#00ffff00"}} top="" controlSize="xl">
                            <Icon36Add style={{marginTop: '19px', color: "#9ec1ea"}}/>
                        </File>
                    }
                </div>
                    {
                        photo3 !== null &&
                        <div style={{color: 'red', textAlign: 'center'}} onClick={()=>setPhoto3(null)}>Удалить</div>
                    }
            </Card>
            </CardGrid>
        </Group>
            <Group header={<Header mode="secondary">Укажите тип отметки</Header>}>
                <FormLayout>
                    <div>
                        <Radio name="radio" value="1" description="Тренога" defaultChecked>Передвижная камера</Radio>
                        {/*<Radio name="radio" value="2" description="Стационарный">Пост ДПС</Radio>*/}
                        <Radio name="radio" value="3" description="Проверки и спецоперации">Мобильная группа ДПС</Radio>
                    </div>
                </FormLayout>
            </Group>
            <Group header={<Header mode="secondary">Завершите добавление</Header>}>
                <Button size="xl" mode="secondary">Добавить на карту</Button>
            </Group>
        </React.Fragment>
    )
};

export default Add;