import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	View,
	Panel,
	PanelHeader,
	Epic,
	Tabbar,
	TabbarItem,
	Button,
	Div,
	List,
	Counter,
	ModalRoot,
	ModalCard, RichCell, Avatar, Card, File, CardGrid, Tabs, TabsItem, ModalPage
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import MapComponent from './components/map';
import Add from './components/add';
import '@vkontakte/vkui/dist/vkui.css';
import PanelMap from './panels/panelMap';
import MarkerInfo from './components/markerInfo';
import Icon24CommentOutline from '@vkontakte/icons/dist/24/comment_outline';

const App = () => {
	const [geoStatus, setGeoStatus] = useState(null);
	if (geoStatus === null){
		bridge.send("VKWebAppGetGeodata", {})
			.then(res=>{
				console.log(res);
				if (res.error_reason === 'User denied'){
					setGeoStatus(false)
				} else {
					setGeoStatus(true)
					setCoords({lat: res.lat, long: res.long, zoom: 13})
				}
			})
			.catch(e=>console.log(e));
	}
	const [coords, setCoords] = useState({lat: 0, long: 0});
	const [modalPage, setModalPage] = useState(null);
	const [activeStory, setActiveStory] = useState("map");
	let modalCard = (
		<ModalRoot activeModal={'marker'} style={{maxHeight: '500px', overflow: 'scroll'}}>
			<ModalCard
				id={'marker'}
				onClose={() => setModalPage(null)}
				//icon={<Icon56MoneyTransferOutline />}
				header="Мобильная группа ДПС"
				caption={
					<MarkerInfo setModalPage={setModalPage} />
				}
			>
			</ModalCard>
		</ModalRoot>
	);
		return (
			<Epic activeStory={activeStory} tabbar={
				<Tabbar>
					<TabbarItem
						onClick={()=>setActiveStory('map')}
						selected={activeStory === 'map'}
						data-story="map"
						text="Карта"
					><Icon28Search /></TabbarItem>
					<TabbarItem
						onClick={()=>setActiveStory('add')}
						selected={activeStory === 'add'}
						data-story="add"
						text="Добавить"
					><Icon28AddOutline /></TabbarItem>
					<TabbarItem
						onClick={()=>setActiveStory('chat')}
						selected={activeStory === 'chat'}
						data-story="chat"
						text="Чатик"
					><Icon24CommentOutline /></TabbarItem>
				</Tabbar>
			}>
				<View id="map" activePanel="map" modal={modalPage}>
					<PanelMap
						id="map"
						coords={coords}
						geoStatus={geoStatus}
						setGeoStatus={setGeoStatus}
						openModalCard={()=>setModalPage(modalCard)}
					/>
				</View>
				<View id="add" activePanel="add">
					<Panel id="add">
						<PanelHeader>Добавить</PanelHeader>
						<Add coords={coords}/>
					</Panel>
				</View>
				<View id="chat" activePanel="chat">
					<Panel id="chat">
						<PanelHeader>Чатик</PanelHeader>
					</Panel>
				</View>
			</Epic>
		);
};

export default App;

