import {
    Cell,
    List,
    ModalCard,
    ModalRoot,
    Panel,
    PanelHeader,
    PanelHeaderButton,
    PanelHeaderContext
} from "@vkontakte/vkui";
import Icon28ChevronDownOutline from '@vkontakte/icons/dist/28/chevron_down_outline';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import MapComponent from "../components/map";
import React, {useState} from "react";
import bridge from "@vkontakte/vk-bridge";

const PanelMap = (props) => {
    const geoStatus = props.geoStatus;
    const setGeoStatus = props.setGeoStatus;
    const coords = props.coords;
    const [filter, setFilter] = useState({zero: true,one: true,two: true,three: true,four: true});
    const [support, openSupport] = useState(false);
    return (
        <Panel id="map">
            <PanelHeader
                left={<PanelHeaderButton onClick={()=>openSupport(!support)}>
                    <Icon28ChevronDownOutline
                        style={{ transform: `rotate(${support ? '180deg' : '0'})` }}
                        onClick={()=>openSupport(!support)}
                    />
                </PanelHeaderButton>}
            >
                Карта
            </PanelHeader>
            <PanelHeaderContext style={{zIndex: 10}} opened={support} onClose={()=>openSupport(!support)}>
                <List>
                    <Cell
                        before={'*marker*'}
                    >Вы здесь</Cell>
                    {/*<Cell*/}
                    {/*	before={'*marker*'}*/}
                    {/*	asideContent={filter.zero === true ? <Icon24Done fill="var(--accent)" /> : null}*/}
                    {/*	onClick={*/}
                    {/*		()=>{*/}
                    {/*			console.log(filter);*/}
                    {/*			filter.zero=!filter.zero;*/}
                    {/*			setFilter(filter);*/}
                    {/*		}*/}
                    {/*	}*/}
                    {/*>Посты ДПС</Cell>*/}
                    {/*<Cell*/}
                    {/*	before={'*marker*'}*/}
                    {/*	asideContent={filter.one === true ? <Icon24Done fill="var(--accent)" /> : null}*/}
                    {/*	onClick={*/}
                    {/*		()=>{*/}
                    {/*			let newFilter = filter;*/}
                    {/*			newFilter.one=!filter.one;*/}
                    {/*			setFilter(newFilter);*/}
                    {/*		}*/}
                    {/*	}*/}
                    {/*>Стационарные камеры</Cell>*/}
                    <Cell
                        description={'Треноги'}
                        before={'*marker*'}
                        asideContent={filter.two === true ? <Icon24Done fill="var(--accent)" /> : null}
                        onClick={
                            ()=>{
                                let newFilter = filter;
                                newFilter.two=!filter.two;
                                setFilter(newFilter);
                            }
                        }
                    >Передвижные камеры</Cell>
                    <Cell
                        description={'Дежурные экипажи'}
                        before={'*marker*'}
                        asideContent={filter.three === true ? <Icon24Done fill="var(--accent)" /> : null}
                        onClick={
                            ()=>{
                                let newFilter = filter;
                                newFilter.three=!filter.three;
                                setFilter(newFilter);
                            }
                        }
                    >Мобильная группа ДПС</Cell>
                </List>
            </PanelHeaderContext>
            {
                geoStatus === false &&
                <Cell style={{zIndex: 5}} onClick={()=>setGeoStatus(null)} multiline>Вы не дали доступ в геопозиции, для более удобной навигации нажмите сюда</Cell>
            }
            <MapComponent
                coords={coords}
                openModalCard={props.openModalCard}
            />
        </Panel>
    )
};

export default PanelMap;