import React, { Component } from 'react';
import {Map, CircleMarker, TileLayer, Marker, Popup} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import {
    Button
} from '@vkontakte/vkui';
import bridge from "@vkontakte/vk-bridge";

class MapComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            lat: 55.753960,
            long: 37.620393,
            zoom: 12
        };
    }

    componentDidMount() {
        if (this.props.coords.lat !== 0){
            this.setState({lat: this.props.coords.lat, long: this.props.coords.long, zoom: this.props.coords.zoom})
        }
    }

    render() {
        const L = require('leaflet');

        const myIcon = L.icon({
            iconUrl: require('./dps.png'),
            iconSize: [64,55],
            iconAnchor: [32, 64],
            popupAnchor: null,
            shadowUrl: null,
            shadowSize: null,
            shadowAnchor: null
        });
            return (
                <div style={{ height: "100%", width: "100%", scroll: "noscroll"}}>
                    <Map
                        zoomControl={false}
                        style={{ height: "100%", width: "100%", position: 'absolute', zIndex: 1}}
                        zoom={this.state.zoom}
                        center={[this.props.coords.lat !== 0 ? this.props.coords.lat : this.state.lat, this.props.coords.long !==0 ? this.props.coords.long : this.state.long]}>
                        <TileLayer url="http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
                        <CircleMarker
                            center={[this.props.coords.lat !== 0 ? this.props.coords.lat : this.state.lat, this.props.coords.long !==0 ? this.props.coords.long : this.state.long]}
                        />
                        <Marker
                            onClick={this.props.openModalCard}
                            icon={myIcon}
                            position={[56.237361899999996,43.4565182]}
                        />
                    </Map>
                </div>
            );
        }
}

export default MapComponent;